            color: '#f2f2f2', // 设置主标题字体颜色
            fontStyle: '', // 主标题文字风格
            fontSize: 16
          }
        },
        tooltip: {
          trigger: 'item',
          // formatter: '{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          right: -0,
          top: 10,
          bottom: 0,
          data: name,
          textStyle: { // 设置主标题风格
            color: '#f2f2f2', // 设置主标题字体颜色
            fontStyle: '', // 主标题文字风格
            fontSize: 12
          }
        },
        series: [
          {
            name: '访问来源',
            type: 'pie',
            radius: '65%',
            center: ['25%', '60%'],
            avoidLabelOverlap: false,
            label: {
              normal: {
                formatter: '{d}%',
                show: false,
                position: 'center'
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: '30',
                  fontWeight: 'bold'
                }
              }
            },
            data: val,
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
      myChart.setOption(option);
    },
    async getreporttypelist(cid, pids) {
      const params = {
        cid,
        pids
      }
      try {
        this.showLoadingDiolod = true
        const { data } = await reporttypelist(params)
        this.cid = cid
        this.reporttype = data.types
        // this.equipmentlistz = []
        const equipmentlistz = []
        for (let i = 0; i < this.equipmentlist.length; i++) {
          for (let j = 0; j < this.reporttype.length; j++) {
            if (this.equipmentlist[i].code == this.reporttype[j]) {
              equipmentlistz.push(this.equipmentlist[i])
            }
          }
        }
        this.equipmentlistz = equipmentlistz

        this.device_type = ''
        for (let i = 0; i < this.equipmentlistz.length; i++) {
          if (this.equipmentlistz[i].checked) {
            this.device_type = this.device_type + this.equipmentlistz[i].code + ','
          }
        }
        this.showLoadingDiolod = false
        this.generate()
      } catch (error) {
        this.showLoadingDiolod = false
        msgUtil.error('查询失败', msgUtil.findErrorMsg(error))
      }
    },
    async getagentlist(cid) {
      try {
        const { data } = await agentlist(cid)
        if (data.level == 'province') {
          if (data.companys_list.length === 0) return
          this.provinceagentlist = data.companys_list
          for (let i = 0; i < this.provinceagentlist.length; i++) {
            if (this.provinceagentlist[i].type_level == 'CH') {
              this.provinceagentlist.splice(i, 1)
            }
          }
        } else if (data.level == 'city') {
          if (data.companys_list.length === 0) return
          this.cityagentlist = data.companys_list
          for (let i = 0; i < this.cityagentlist.length; i++) {
            if (this.cityagentlist[i].type_level == 'CH') {
              this.cityagentlist.splice(i, 1)
            }
          }
        } else if (data.level == 'county') {
          if (data.companys_list.length === 0) return
          this.countyagentlist = data.companys_list
          for (let i = 0; i < this.countyagentlist.length; i++) {
            if (this.countyagentlist[i].type_level == 'CH') {
              this.countyagentlist.splice(i, 1)
            }
          }
        } else if (data.level == 'village') {
          // this.villageagentlist = data.companys_list
        }
      } catch (error) {
        msgUtil.error('查询失败', msgUtil.findErrorMsg(error))
      }
    },
    async getplaceslist(code) {
      try {

        const { data } = await placeslist(code)
        this.placeslist = data
      } catch (error) {
        msgUtil.error('查询失败', msgUtil.findErrorMsg(error))
      }
    },
    deleteChecked(item) {
      item.checked = !item.checked
      this.equipmentlistz
    },
    choseequipment() {
      this.showequipment = !this.showequipment
    },
    changecheck(item) {
      item.checked = !item.checked
    },
    getreporttypelistall() {
      this.getreporttypelist(this.cid, this.pids)
    },
    allchangechecked() {
      for (let i = 0; i < this.equipmentlistz.length; i++) {
        this.equipmentlistz[i].checked = true
      }
      this.allchecked = true
      this.dischecked = false
    },
    dischangechecked() {
      for (let i = 0; i < this.equipmentlistz.length; i++) {
        this.equipmentlistz[i].checked = false
      }
      this.dischecked = true
      this.allchecked = false
    },
    allcancel() {
      this.showequipment = !this.showequipment
    }
  }
}
</script>

